.header {
	transition: 0.3s;
	position: fixed;
	z-index: 11;
	top: 0;
	left: 0;
	width: 100%;
	&.menu-open {
		background-color: #C1372E;
		.header__logo {
			&--site {
				display: none; }
			&--mobile {
				display: none; } }
		.header__phone--mobile {
			display: none; }
		.header__logo--menu {
			display: block; }
		.header__inner {
			&:after {
				content: none; }
			&:before {
				content: none; } } }
	&.is-scroll {
		background-color: #FFFFFF;
		.header__inner {
			&:after {
				content: none; }
			&:before {
				width: 100%; } }
		.header__phone {
			color: #252838; }
		.header__phone--mobile {
			color: #E5372F; }
		.header__tablet-phone {
			color: #252838; }
		.burger {
			span {
				background-color: #252838; } }
		.log-link {
			color: #252838;
			@include tablet {
				color: #ffffff; }
			.icon-user {
				fill: #7C859C;
				@include tablet {
					fill: #ffffff; } } }
		.header__btn {
			.btn {
				color: #252838;
				border: 1px solid #E6E6E6;
				@include tablet {
					color: #ffffff; }
				&:hover {
					background-color: #E7EAEF; } } } }
	&__logo {
		width: 172px;
		&--menu {
			display: none; }
		&--site {
			@include xs {
				display: none; } }
		&--mobile {
			display: none;
			filter: brightness(0.5);
			@include xs {
				display: block;
				width: 71px; } } }
	&__inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 31px 0 31px;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: calc(100% - 430px);
			border-bottom: 1px solid rgba(124, 133, 156, 0.5);
			@include r(1200) {
				width: calc(100% - 390px); }
			@include sm {
				content: none; } }
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			right: 0;
			width: 430px;
			border-bottom: 1px solid rgba(255, 255, 255, 0.5);
			@include r(1200) {
				width: 390px; }
			@include sm {
				content: none; } }
		@include xs {
			padding: 22px 0; } }
	&__content {
		display: flex;
		align-items: center;
		@include tablet {
			transition: 0.2s;
			align-items: flex-start;
			flex-direction: column;
			position: absolute;
			bottom: 1px;
			left: 0;
			width: calc(100% + 80px);
			margin: 0 -40px;
			background-color: #ffffff;
			transform: translate(100%, 100%);
			padding: 0 40px;
			height: calc(100vh - 88px);
			overflow: auto;
			&.is-open {
				transform: translate(0, 100%); } }
		@include md {
			height: calc(100vh - 88px);
			width: calc(100% + 40px);
			margin: 0 -20px;
			padding: 0 20px; }
		@include xs {
			height: calc(100vh - 70px); } }
	&__log {
		display: flex;
		align-items: center;
		margin-left: 68px;
		justify-content: flex-end;
		width: 210px;
		@include r(1250) {
			margin-left: 40px; }
		@include tablet {
			border-top: 1px solid rgba(232, 232, 232, 0.35);
			order: 1;
			width: calc(100% + 80px);
			margin: 0 -40px;
			padding: 25px 40px 28px;
			justify-content: space-between;
			background-color: #C1372E; }
		@include sm {
			padding: 25px 20px 28px;
			margin: 0 -20px;
			width: calc(100% + 40px); } }
	&__btn {
		margin-left: 32px;
		margin-bottom: -3px; }
	&__menu {
		@include tablet {
			order: 3;
			padding: 35px 0; } }
	&__phone {
		margin-left: 82px;
		color: #FFFFFF;
		font-size: 16px;
		@include medium;
		@include r(1200) {
			margin-left: 50px; }
		@include tablet {
			order: 2;
			color: #252838;
			font-size: 30px;
			margin-left: 0;
			text-align: center;
			width: 100%;
			padding: 36px 0 31px;
			border-bottom: 1px solid #7C859C; }
		a {
			text-decoration: none; }
		&--mobile {
			display: none;
			@include sm {
				display: block;
				width: auto;
				order: 0;
				border-bottom: none;
				padding: 0;
				color: #E5372F; }
			@include xs {
				font-size: 18px; } } }
	&__tablet {
		display: none;
		align-items: center;
		justify-content: space-between;
		width: 350px;
		@include tablet {
			display: flex; }
		@include sm {
			display: block;
			width: auto; } }
	&__tablet-phone {
		color: #FFFFFF;
		font-size: 16px;
		@include medium;
		a {
			text-decoration: none; }
		@include sm {
			display: none; } } }

.burger {
	cursor: pointer;
	width: 26px;
	span {
		transition: 0.3s;
		background-color: #ffffff;
		height: 2px;
		display: block;
		width: 100%;
		&:not(:last-child) {
			margin-bottom: 8px; } }
	&.is-active {
		span {
			&:nth-child(1) {
				transform: translate(1px, 5px) rotate(45deg); }
			&:nth-child(2) {
				transform: translate(1px, -5px) rotate(-45deg); } } } }

.menu {
	display: flex;
	@include medium;
	font-size: 14px;
	@include tablet {
		display: block;
		font-size: 20px;
		@include regular; }
	&__item {
		&:not(:last-child) {
			margin-right: 35px;
			@include r(1250) {
				margin-right: 20px; }
			@include tablet {
				margin-right: 0;
				margin-bottom: 20px; } } }
	&__link {
		color: #000000;
		transition: 0.3s;
		text-decoration: none;
		position: relative;
		@include tablet {
			&:after {
				content: none; } }
		&:hover {
			color: #C1372E; }
		&:after {
			content: '';
			position: absolute;
			bottom: -37px;
			left: 0;
			height: 4px;
			width: 0;
			background-color: #C1372E; }
		&.is-active {
			&:after {
				width: 100%; } } } }

.logo {
	display: inline-block;
	width: 100%;
	img {
		width: 100%; } }
