.about {
	padding: 0 0 120px;
	@include tablet {
		padding: 0 0 91px; }
	@include xs {
		padding: 0 0 49px; }
	&__title {
		margin-bottom: 69px;
		@include xs {
			margin-bottom: 30px; } }
	&__subtitle {
		font-size: 24px;
		line-height: 1.6;
		max-width: 770px;
		margin-bottom: 69px;
		@include sm {
			font-size: 20px;
			margin-bottom: 59px;
			line-height: 1.5; } }
	&__items {
		display: flex;
		justify-content: space-between;
		@include tablet {
			justify-content: flex-start; }
		@include sm {
			display: block; } }
	&__item {
		max-width: 500px;
		@include tablet {
			max-width: 440px; }
		&:not(:last-child) {
			margin-right: 31px;
			@include sm {
				margin-right: 0;
				margin-bottom: 49px; } } } }

.about-item {
	&__icon {
		height: 51px;
		margin-bottom: 22px;
		font-size: 52px;
		.icon {
			fill: #E53630; }
		&--sm {
			font-size: 45px; }
		@include xs {
			margin-bottom: 24px; } }
	&__title {
		font-size: 24px;
		line-height: 1.6;
		margin-bottom: 10px;
		@include xs {
			font-size: 20px; } }
	&__text {
		color: #7C859C;
		line-height: 2;
		font-size: 14px; } }
