.help {
	padding: 171px 0 98px;
	text-align: center;
	@include xs {
		padding: 110px 0 55px; }
	&__text {
		max-width: 470px;
		margin: 0 auto 47px;
		line-height: 1.75;
		@include xs {
			line-height: 2;
			max-width: 300px;
			margin-bottom: 34px; } }
	&__title {
		margin-bottom: 29px;
		position: relative;
		z-index: 1;
		@include xs {
			max-width: 230px;
			margin: 0 auto 17px; }
		.icon-phone {
			z-index: -1;
			position: absolute;
			height: 111px;
			width: 111px;
			stroke: #E7EAEF;
			left: 50%;
			bottom: 0;
			transform: translate(-50%, -8px); } }
	&__phone {
		font-size: 42px;
		color: #E53630;
		a {
			text-decoration: none; }
		@include xs {
			font-size: 30px; } } }
