.hero {
	padding: 120px 0 80px;
	position: relative;
	@include xs {
		padding: 80px 0 42px; }
	&__rect {
		position: absolute;
		height: calc(100% - 80px);
		top: 0;
		right: 0;
		width: calc(430px + (100% - 1200px)/2);
		background-color: #C1372E;
		@include r(1200) {
			width: calc(390px + 40px); }
		@include md {
			height: 0;
			padding-top: 58%;
			width: calc(390px + 20px); }
		@include sm {
			width: 66px;
			padding-top: 60%; }
		@include xs {
			padding-top: 61.3%; } }
	&__inner {
		display: flex;
		align-items: flex-end;
		@include md {
			flex-direction: column; } }
	&__content {
		width: 50%;
		@include tablet {
			width: 54.4%; }
		@include md {
			order: 2;
			width: 100%; } }
	&__content-top {
		margin-left: 50px;
		@include tablet {
			margin-left: 0; } }
	&__title {
		font-size: 54px;
		@include bold;
		line-height: 1.17;
		margin-bottom: 24px;
		@include sm {
			font-size: 30px;
			margin-bottom: 20px;
			max-width: 300px; } }
	&__desc {
		margin-bottom: 55px;
		@include md {
			font-size: 16px; }
		@include sm {
			line-height: 1.8;
			margin-bottom: 35px;
			max-width: 280px; } }
	&__search {
		margin-bottom: 97px;
		max-width: 500px;
		@include xs {
			margin-bottom: 71px;
			max-width: none; } }
	&__media {
		width: 50%;
		padding-left: 30px;
		position: relative;
		@include tablet {
			width: 45.6%; }
		@include md {
			margin-bottom: 41px;
			order: 1;
			width: 100%;
			padding-left: 0;
			display: flex;
			justify-content: flex-end; } }
	&__image {
		overflow: hidden;
		width: 100%;
		position: relative;
		padding-top: 123%;
		@include tablet {
			padding-top: 150%; }
		@include md {
			width: 70%;
			padding-top: 50%; }
		@include xs {
			width: 74%;
			padding-top: 45%; }
		img {
			object-fit: cover;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			@include xs {
				height: 130%; } } }
	&__tooltip {
		position: absolute;
		bottom: 0;
		right: 0;
		transform: translateX(40px);
		width: 230px;
		@include md {
			transform: translateX(20px); }
		@include sm {
			display: none; } } }

.tooltip {
	width: 100%;
	background-color: #E7EAEF;
	padding: 20px 0 30px;
	&__title {
		padding-left: 33px;
		margin-bottom: 5px; }
	&__content {
		padding: 5px 33px 0; }
	&__item {
		& + & {
			margin-top: 38px; } }
	&__label {
		margin-bottom: 9px;
		&--sm {
			font-size: 14px;
			margin-bottom: 5px; } }
	&__text {
		font-size: 14px;
		color: #7C859C;
		line-height: 1.4; } }

.hero-info {
	&__items {
		margin-top: 35px;
		display: flex;
		margin-bottom: 17px;
		justify-content: space-between;
		@include md {
			justify-content: flex-start; }
		@include xs {
			margin-top: 20px; } }
	&__item {
		max-width: 235px;
		//width: 50%
		margin-right: 34px;
		@include xs {
			max-width: 158px;
			margin-right: 0; }
		&:not(:last-child) {
			@include xs {
				margin-right: 20px; } } }

	&__title {
		font-size: 24px;
		margin-bottom: 12px;
		@include sm {
			font-size: 20px;
			margin-bottom: 16px; } }
	&__text {
		line-height: 2;
		font-size: 14px;
		max-width: 233px;
		color: #7C859C;
		@include xs {
			font-size: 12px;
			line-height: 1.8; } } }
