.banner {
	padding: 79px 0;
	background-color: #C1372E;
	@include tablet {
		padding: 79px 0 49px; }
	@include xs {
		padding: 54px 0 71px; }
	&__inner {
		display: flex;
		justify-content: space-between;
		@include sm {
			display: block; } }
	&__btn {
		align-self: center;
		@include tablet {
			flex-grow: 1;
			display: flex;
			justify-content: flex-end; }
		@include sm {
			justify-content: center; } }

	&__step {
		@include tablet {
			max-width: 310px; }
		@include md {
			max-width: 260px; }
		@include sm {
			max-width: none;
			margin-bottom: 68px; }
		@include xs {
			margin-right: -20px; } } }

.info-banner {
	background-color: #E7EAEF;
	padding: 102px 0 53px;
	@include tablet {
		padding: 55px 0 3px; }
	@include md {
		padding: 50px 0 45px; }
	&__inner {
		display: flex;
		justify-content: space-between;
		@include tablet {
			align-items: center; }
		@include md {
			flex-direction: column;
			align-items: flex-start; } }
	&__title {
		padding-left: 50px;
		font-size: 42px;
		line-height: 1.2;
		@include regular;
		margin-right: 50px;
		@include tablet {
			padding-left: 0;
			margin-bottom: 60px; }
		@include sm {
			font-size: 30px;
			margin-bottom: 30px; } }
	&__list {
		width: 50%;
		@include r(1250) {
			width: 60%; }
		@include tablet {
			width: 50%; }
		@include md {
			width: 100%;
			padding-left: 57px; } } }

.list {
	margin-right: -20px;
	display: flex;
	flex-wrap: wrap;
	color: #7C859C;
	justify-content: space-between;
	@include tablet {
		justify-content: flex-start; }
	@include md {
		margin-right: 0;
		display: block; }
	&__item {
		line-height: 1.5;
		position: relative;
		padding-left: 28px;
		width: 175px;
		margin-bottom: 49px;
		margin-right: 10px;
		@include tablet {
			margin-right: 51px; }
		@include md {
			width: auto;
			margin-right: 0;
			margin-bottom: 14px;
			br {
				display: none; } }
		@include xs {
			padding-left: 14px; }
		&:before {
			content: '';
			height: 6px;
			width: 6px;
			border-radius: 50%;
			background-color: #7C859C;
			position: absolute;
			top: 8px;
			left: 0;
			font-size: 20px;
			@include xs {
				height: 4px;
				width: 4px; } } } }

