.steps {
	&__heading {
		margin-bottom: 69px;
		@include xs {
			max-width: 300px; } }
	&__content {
		padding: 100px 0;
		@include xs {
			padding: 50px 0 70px; } }
	&__items {
		display: flex;
		justify-content: space-between;
		@include sm {
			display: block; } }
	&__item {
		@include xs {
			margin-right: -20px; }
		&:not(:last-child) {
			margin-right: 5px;
			@include sm {
				margin-right: 0;
				margin-bottom: 70px; }
			@include xs {
				margin-right: -20px; } } }
	&__middle {
		padding: 38px 0 55px;
		background-color: #C1372E;
		@include xs {
			padding: 38px 0 60px; } } }

.step {
	margin-top: 33px;
	position: relative;
	z-index: 1;
	padding: 0 0 0 76px;
	@include tablet {
		padding: 0 0 0 30px; }
	@include sm {
		padding: 0 0 0 76px; }
	@include xs {
		transform: translateX(-20px); }
	&__number {
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;
		transform: translateY(-30%);
		@include light;
		color: #E7EAEF;
		font-size: 120px;
		line-height: 0.83;
		opacity: 0.5;
		@include xs {
			transform: translateY(-39%); } }
	&__title {
		font-size: 42px;
		margin-bottom: 20px;
		line-height: 1;
		@include md {
			font-size: 30px; }
		@include xs {
			line-height: 1.2;
			margin-bottom: 8px; } }
	&__text {
		color: #7C859C;
		line-height: 1.75;
		max-width: 247px;
		@include tablet {
			max-width: 270px; }
		@include xs {
			max-width: none; } }
	&--middle {
		color: #FFFFFF;
		display: flex;
		@include tablet {
			padding: 0 0 0 75px; }
		@include sm {
			display: block; }
		@include xs {
			margin-right: -20px; }
		.step__number {
			opacity: 0.1; }
		.step__title {
			width: 46.6%;
			transform: translateX(-25px);
			@include sm {
				transform: none; }
			@include xs {
				width: auto;
				margin-bottom: 8px; } }
		.step__text {
			max-width: 530px;
			color: #FFFFFF;
			@include tablet {
				margin-top: -12px;
				max-width: 480px; }
			@include md {
				max-width: 360px; }
			@include xs {
				margin-top: 0; } } }
	&--banner {
		padding: 0 0 0 60px;
		color: #FFFFFF;
		@include tablet {
			padding: 0 0 0 39px; }
		@include xs {
			padding: 0 0 0 59px; }
		.step__number {
			opacity: 0.1; }
		.step__title {
			font-size: 24px;
			line-height: 1.25;
			max-width: 360px;
			@include md {
				font-size: 20px; } } } }

