@include font(Roboto-Regular, Roboto-Regular);
@include font(Roboto-Medium, Roboto-Medium);
@include font(Roboto-Bold, Roboto-Bold);
@include font(Roboto-Light, Roboto-Light);

@mixin regular {
	font-family: Roboto-Regular, Helvetica, Arial;
	font-weight: 400; }

@mixin medium {
	font-family: Roboto-Medium, Helvetica, Arial;
	font-weight: 500; }

@mixin bold {
	font-family: Roboto-Bold, Helvetica, Arial;
	font-weight: 600; }

@mixin light {
	font-family: Roboto-Light, Helvetica, Arial;
	font-weight: 300; }


body {
	@include smooth-font;
	@include regular;
	background: #FEFEFE;
	font-size: 16px;
	color: #252838;
	&.is-hidden {
		height: 100%;
		width: 100%;
		position: fixed;
		overflow: hidden; }
	@include md {
		font-size: 14px; } }

a {
	font-size: inherit;
	color: inherit; }

.line-title {
	@include medium;
	letter-spacing: 3px;
	font-size: 10px;
	text-transform: uppercase;
	color: #7C859C;
	position: relative;
	line-height: 2.7;
	display: flex;
	align-items: center;
	&:after {
		flex-grow: 1;
		content: '';
		background-color: #7C859C;
		opacity: 0.5;
		height: 1px;
		margin-left: 16px; } }

.section-title {
	@include bold;
	font-size: 42px;
	span {
		color: #E53630; }
	@include sm {
		font-size: 30px; } }

.text {
	line-height: 1.75;
	@include xs {
		line-height: 2; } }

.dark-bg {
	background-color: #F7F7F7; }

.tablet-hide {
	@include tablet {
		display: none; } }
