.search {
	z-index: 2;
	position: relative;
	&__input-wrap {
		position: relative;
		width: 100%;
		@include xs {
			margin-bottom: 20px; } }
	&__input {
		z-index: 2;
		position: relative;
		width: 350px;
		padding: 12px 60px 14px 30px;
		line-height: 1.3;
		border-radius: 25px;
		border: 1px solid rgba(124, 133, 156, 0.5);
		font-size: 14px;
		color: #252838;
		@include regular;
		@include placeholder-color(#ABB1C3);
		transition: 0.3s;
		@include xs {
			font-size: 13px;
			text-align: center;
			width: 100%;
			padding: 14px 10px 14px 10px; }
		&.is-error {
			border-color: rgba(193, 55, 46, 0.51); } }
	&__btn {
		z-index: 2;
		position: absolute;
		top: 0;
		right: 0;
		@include xs {
			position: static;
			//display: flex
			//justify-content: center
			.btn {
				width: 100%; } } }
	&__results {
		display: none;
		z-index: 1;
		background-color: #FFFFFF;
		padding: 30px 0 10px;
		position: absolute;
		bottom: 50%;
		left: 0;
		width: 100%;
		transform: translateY(100%);
		border-radius: 0 0 25px 25px;
		border-width: 0 1px 1px 1px;
		border-style: solid;
		border-color: rgba(124, 133, 156, 0.5);
		//max-height: 245px
		//overflow: auto
		&.is-visible {
			display: block; } } }

.search-item {
	font-size: 14px;
	cursor: pointer;
	transition: 0.3s;
	padding: 10px 29px;
	a {
		text-decoration: none; }
	@include xs {
		padding: 10px 15px; }
	&.is-selected {
		background-color: #E7EAEF!important; }
	&:hover {
		background-color: rgba(231, 234, 239, 0.6); }
	&__title {
		margin-bottom: 5px; }
	&__info {
		color: #7C859C;
		line-height: 1.4;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden; } }

