.partners {
	padding: 122px 0 65px;
	@include sm {
		padding: 70px 0 40px; }
	&__title {
		margin-bottom: 30px;
		@include xs {
			margin-bottom: 23px; } }
	&__text {
		max-width: 480px;
		margin-bottom: 70px;
		@include xs {
			margin-bottom: 52px;
			.text {
				line-height: 1.7; } } }
	&__logos-container {
		display: flex;
		justify-content: flex-end; }
	&__logos {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		max-width: 750px; }
	&__logo {
		width: 33.33%;
		margin-bottom: 56px;
		padding-right: 40px;
		img {
			max-width: 100%; }
		@include xs {
			margin-bottom: 29px; } } }
