.results {
	background-color: #FFFFFF;
	padding: 93px 0 58px;
	@include sm {
		padding: 37px 0 0; }
	&__top {
		width: 50%;
		.line-title {
			margin-bottom: 52px;
			@include xs {
				margin-bottom: 30px; } }
		@include tablet {
			width: auto;
			max-width: 600px; } }
	&__head {
		padding-left: 50px;
		@include xs {
			padding-left: 57px; } }
	&__title {
		margin-bottom: 48px;
		@include sm {
			margin-bottom: 22px; } }
	&__desc {
		font-size: 24px;
		@include tablet {
			margin-bottom: 7px; }
		@include sm {
			font-size: 20px;
			margin-bottom: 30px; } }
	&__rect-container {
		position: relative;
		margin-bottom: 99px;
		@include tablet {
			margin-bottom: 84px; }
		@include sm {
			display: none; } }
	&__rect {
		position: absolute;
		left: 50%;
		width: 50%;
		height: 400px;
		background-color: #E7EAEF; }
	&__sliders {
		position: relative; }
	&__images {
		position: relative;
		width: 100%;
		padding-top: 58.34%;
		@include sm {
			padding-top: 59.7%; } }
	&__images-slider {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		div {
			height: 100%; } }
	&__image {
		height: 100%;
		width: 100%;
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	&__testimonials-container {
		width: 50%;
		position: absolute;
		bottom: 0;
		left: 0;
		@include tablet {
			width: auto;
			max-width: 600px; }
		@include md {
			max-width: 450px; }
		@include sm {
			max-width: none;
			position: static;
			margin: 0 -20px; } }
	&__quotes {
		position: absolute;
		left: 0;
		top: 0;
		transform: translate(-45%, -65%);
		@include light;
		font-size: 200px;
		line-height: 0.5;
		color: #E53630;
		z-index: 1;
		@include md {
			font-size: 120px; }
		@include sm {
			padding-top: 48%;
			transform: translate(-45%, 0); } }
	&__controls {
		padding: 70px 0 65px;
		display: flex;
		justify-content: flex-end;
		@include sm {
			padding: 11px 20px 8px;
			background-color: #F7F7F7;
			margin: 0 -20px; } } }

.testimonial {
	display: flex!important;
	flex-direction: column;
	background-color: #F7F7F7;
	@include sm {
		padding-bottom: 50px; }
	@include xs {
		padding-bottom: 0; }
	&__content {
		padding: 50px 88px 50px 50px;
		flex-grow: 1;
		@include md {
			padding: 30px; }
		@include sm {
			padding: 20px 20px 30px; } }
	&__title {
		font-size: 24px;
		line-height: 1.6;
		margin-bottom: 20px;
		@include md {
			line-height: 1.4;
			font-size: 20px;
			margin-bottom: 16px; } }
	&__text {
		@include xs {
			font-size: 16px;
			line-height: 1.75; } }
	&__bottom {
		display: flex;
		padding: 0 50px 30px 50px;
		justify-content: space-between;
		@include md {
			padding: 0 30px 30px; }
		@include sm {
			padding: 0 20px 46px; }
		@include xs {
			display: block; } }
	&__item {
		&:last-child {
			margin-left: 20px;
			padding-left: 19px;
			position: relative;
			@include xs {
				margin-top: 21px;
				margin-left: 0; }
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 1px;
				opacity: 0.9;
				background-color: rgba(124, 133, 156, 0.65); } } }
	&__link {
		text-align: right;
		background-color: #FFFFFF;
		padding: 15px 0;
		font-size: 14px;
		color: #7C859C;
		a:hover {
			color: #121313; }
		@include sm {
			background-color: transparent;
			padding: 15px 20px; }
		@include xs {
			position: absolute;
			bottom: 0;
			width: 100vw;
			padding: 15px 20px 46px; } } }


.testimonial-item {
	&__title {
		@include medium;
		margin-bottom: 5px;
		font-size: 16px;
		&--sm {
			font-size: 14px; } }
	&__text {
		font-size: 14px;
		color: #7C859C; } }

.slider-btn {
	background-color: transparent;
	height: 22px;
	width: 33px;
	.icon {
		height: 100%;
		width: 100%;
		fill: #121313;
		transition: 0.3s; }
	&:hover {
		.icon {
			fill: #C1372E; } }
	&.disable {
		pointer-events: none;
		.icon {
			opacity: 0.2; } } }

.slider-next {
	margin-left: 30px;
	@include sm {
		margin-left: 20px; } }

