.conditions {
	padding-bottom: 120px;
	@include tablet {
		padding-top: 56px;
		padding-bottom: 91px; }
	@include xs {
		padding-top: 0;
		padding-bottom: 72px; }
	&__main {
		display: flex;
		align-items: center;
		margin-bottom: 121px;
		@include tablet {
			margin-bottom: 134px; }
		@include sm {
			display: block;
			margin-bottom: 70px; } }
	&__left {
		width: 50%;
		transform: translateX(-38px);
		@include r(1250) {
			transform: none;
			padding-right: 50px; }
		@include sm {
			width: 100%;
			padding-right: 0;
			margin-bottom: 92px; } }
	&__image-container {
		position: relative;
		width: 100%;
		padding-top: 76.7%;
		z-index: 1;
		max-width: 550px;
		@include tablet {
			padding-top: 75.5%; }
		@include xs {
			padding-top: 75%; } }
	&__image {
		position: absolute;
		display: block;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transform: translateX(-82px);
		@include r(1366) {
			transform: none; }
		@include r(1250) {
			width: 85%; }
		@include tablet {
			width: 89%; }
		@include xs {
			width: 89.5%;
			transform: translateX(-20px); }
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	&__image-desc {
		position: absolute;
		bottom: -16px;
		right: 82px;
		transform: translateY(100%);
		font-size: 14px;
		color: #7C859C;
		a:hover {
			color: #252838; }
		@include r(1366) {
			right: 0; }
		@include r(1250) {
			right: 15%; }
		@include tablet {
			right: 0;
			width: 100%;
			text-align: center; }
		@include sm {
			right: auto;
			left: 0;
			text-align: left;
			width: 89%; }
		@include xs {
			bottom: -5px;
			line-height: 1.4;
			width: 80%; }
		@include r(374) {
			width: 66%; } }
	&__rect {
		position: absolute;
		bottom: 0;
		right: 0;
		background-color: #E7EAEF;
		height: 306px;
		width: 300px;
		z-index: -1;
		@include r(1366) {
			transform: translateX(50px); }
		@include r(1250) {
			transform: none; }
		@include tablet {
			height: 212px;
			width: 212px;
			transform: translateX(10px); }
		@include xs {
			transform: translate(20px, 28px);
			width: 115px;
			height: 190px; } }
	&__right {
		width: 50%;
		margin-top: 8px;
		@include tablet {
			margin-top: 15px; }
		@include sm {
			width: 100%;
			margin-top: 0; } }
	&__title {
		margin-bottom: 28px;
		@include xs {
			margin-bottom: 20px; } }
	&__text {
		margin-bottom: 70px;
		max-width: 580px;
		@include tablet {
			max-width: 460px;
			margin-bottom: 44px; }
		@include xs {
			margin-bottom: 30px; } }
	&__items {
		display: flex;
		@include sm {
			display: block; } }
	&__item {
		max-width: 342px;
		@include tablet {
			max-width: 290px; }
		@include sm {
			max-width: 350px; }
		&:not(:last-child) {
			margin-right: 48px;
			@include tablet {
				margin-right: 22px; }
			@include sm {
				margin-right: 0;
				margin-bottom: 44px; } } } }

.conditions-item {
	&__icon {
		display: flex;
		align-items: flex-end;
		font-size: 54px;
		height: 54px;
		margin-bottom: 39px;
		.icon {
			stroke: #E53630; }
		@include sm {
			margin-bottom: 29px; } }
	&__title {
		font-size: 42px;
		line-height: 1;
		margin-bottom: 21px;
		@include md {
			font-size: 30px; }
		@include sm {
			margin-bottom: 17px; } }
	&__text {
		color: #7C859C;
		@include xs {
			line-height: 1.7; } } }

