.footer {
	background-color: #FFFFFF;
	padding: 15px 0;
	color: #7C859C;
	&__top {
		padding: 41px 0 42px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid rgba(124, 133, 156, 0.5);
		@include sm {
			padding: 24px 0 18px;
			display: block; } }
	&__logo {
		margin-right: 70px;
		width: 71px;
		img {
			width: 100%; }
		@include sm {
			margin: 0 auto 50px; } }
	&__bottom {
		display: flex;
		padding: 38px 0;
		font-size: 12px;
		@include sm {
			display: block; } }
	&__links {
		width: 50%;
		span + span {
			margin-left: 18px; }
		a {
			transition: 0.3s;
			&:hover {
				color: #252838;
				text-decoration-color: #7C859C; } }
		@include sm {
			width: 100%;
			margin-bottom: 50px; } }
	&__info {
		width: 50%;
		line-height: 1.67;
		p + p {
			margin-top: 10px; }
		@include sm {
			width: 100%; } } }

.footer-menu {
	display: flex;
	text-transform: uppercase;
	@include medium;
	font-size: 10px;
	@include sm {
		flex-wrap: wrap;
		justify-content: space-around; }
	&__item {
		letter-spacing: 3px;
		@include tablet {
			letter-spacing: 3.7px; }
		@include md {
			letter-spacing: 3px; }
		@include sm {
			padding: 0 10px; }
		&:not(:last-child) {
			margin-right: 44px;
			@include md {
				margin-right: 0;
				margin-bottom: 24px; } } }
	&__link {
		text-decoration: none;
		transition: 0.3s;
		&:hover {
			color: #252838; } } }

