.btn {
	@include medium;
	font-size: 14px;
	border-radius: 25px;
	padding: 12px 31px 14px;
	text-decoration: none;
	transition: 0.3s;
	background-color: #E53630;
	color: #FFFFFF;
	line-height: 1.47;
	white-space: nowrap;
	display: inline-block;
	&:hover {
		background-color: #C1372E; }
	&--white {
		color: #252838;
		background-color: #FFFFFF;
		&:hover {
			background-color: #E7EAEF; } }
	&--transparent {
		color: #FFFFFF;
		background-color: transparent;
		border: 1px solid #FFFFFF;
		&:hover {
			background-color: #FFFFFF;
			color: #C1372E; } }
	&--sm {
		padding: 6px 23px 7px;
		font-size: 12px; } }

.log-link {
	color: #FFFFFF;
	text-decoration: none;
	display: flex;
	align-items: center;
	font-size: 14px;
	&:hover {
		span {
			text-decoration: underline; } }
	.icon-user {
		fill: #FFFFFF;
		margin-right: 6px;
		height: 14px;
		width: 16px; } }


